.mainpic{
    
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),url("./mainpic1.png");
    background-repeat: no-repeat;
    background-position-x: +45%;
    background-position-y: +10%;
    background-size: cover;
    text-align: center;
    width: 100%;
    min-height: 40%;
    height: 500px;
    opacity: 1;
    background-attachment: scroll;
    position: relative;
    
}

.middle{

    padding: 10% 10%;

}


.h1{

    color: rgb(255, 255, 255);
    font-size: 50px;

}

.text{

    color: rgb(255, 255, 255);
    font-size: 20px;

}