.container{

    display: grid;
    justify-content: center;
    padding: 20px;
    grid-template-columns: repeat(auto-fill, minmax(170px, 2fr));
    gap: 15px;

}

.item{

    background-color: 	#F5F5F5;
    text-align: center;
    font-size: 18;
    border-radius: 5%;
    filter: brightness(95%);

}

.item:hover{

    background-color: #b6b5b5;
    transition: .4s;
    transform: scale(1.08);
    filter: brightness(115%);

}

.item-image{

    height: 150px;
    width: 150px;

}